// components/ProjectDetail.tsx

"use client";

import React, { JSX } from "react";
import { useParams, useNavigate } from "react-router-dom";
import "./ProjectDetail.css";
import projects from "../../data/projects";
import NotFound from "../not-found/NotFoundPage";
import { CONTENT_THEME_TOKENS, type ThemeType } from "../../theme/tokens";

interface ProjectDetailProps {
  theme: ThemeType;
}

// Helper to turn Markdown-style [label](url) and raw URLs into <a> tags
function parseTextWithLinks(text: string) {
  const elements: (string | JSX.Element)[] = [];
  // Regex matches either [label](url) or raw http(s)://… URLs
  const linkRegex = /\[([^\]]+)\]\((https?:\/\/[^\s)]+)\)|(https?:\/\/[^\s]+)/g;
  let lastIndex = 0;
  let match: RegExpExecArray | null;
  let key = 0;

  while ((match = linkRegex.exec(text)) !== null) {
    const [fullMatch, mdLabel, mdUrl, rawUrl] = match;
    const index = match.index;

    // push any text before this match
    if (lastIndex < index) {
      elements.push(text.slice(lastIndex, index));
    }

    // determine which link form matched
    const url = mdUrl || rawUrl!;
    const label = mdLabel || rawUrl!;

    elements.push(
      <a
        key={key++}
        href={url}
        target="_blank"
        rel="noopener noreferrer"
        style={{ color: "inherit", textDecoration: "underline" }}
      >
        {label}
      </a>
    );

    lastIndex = index + fullMatch.length;
  }

  // push remaining text
  if (lastIndex < text.length) {
    elements.push(text.slice(lastIndex));
  }

  return elements;
}

const ProjectDetail: React.FC<ProjectDetailProps> = ({ theme }) => {
  const { projectId } = useParams<{ projectId: string }>();
  const navigate = useNavigate();
  const themes = CONTENT_THEME_TOKENS;

  const project = projects.find((p) => p.id === projectId);

  // If project not found, show a message and a button to go back
  if (!project) {
    return <NotFound theme={theme} backPath="/portfolio" />;
  }

  const LanguageBadges = () => (
    <div
      style={{
        display: "flex",
        flexWrap: "wrap",
        gap: "10px",
        marginBottom: "20px",
      }}
    >
      {project.languages.map((lang) => (
        <span
          key={lang}
          style={{
            padding: "5px 10px",
            backgroundColor: themes[theme]["--button-bg-light"],
            color: themes[theme]["--color-text"],
            borderRadius: "15px",
            fontSize: "14px",
          }}
        >
          {lang}
        </span>
      ))}
    </div>
  );

  const renderSectionContent = (
    section: (typeof project.sections)[number],
    idx: number
  ) => {
    if (section.text) {
      return (
        <p key={`txt-${idx}`} style={{ lineHeight: 1.6, marginBottom: "1.2em" }}>
          {parseTextWithLinks(section.text)}
        </p>
      );
    }

    if (section.video) {
      return (
        <div
          key={`vid-${idx}`}
          style={{
            width: "100%",
            maxWidth: "600px",
            margin: "30px auto",
            aspectRatio: "16/9",
          }}
        >
          <iframe
            width="100%"
            height="100%"
            src={section.video}
            loading="lazy"
            title={`${project.name} demo video`}
            frameBorder={0}
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            referrerPolicy="strict-origin-when-cross-origin"
            allowFullScreen
            style={{ borderRadius: "8px" }}
          />
        </div>
      );
    }

    if (section.image) {
      return (
        <div
          key={`img-${idx}`}
          style={{
            width: "100%",
            aspectRatio: "16 / 9",
            borderRadius: "12px",
            overflow: "hidden",
            margin: "30px 0",
          }}
        >
          <img
            src={section.image}
            alt={`${project.name} screenshot ${idx + 1}`}
            loading="lazy"
            decoding="async"
            style={{ width: "100%", height: "100%", objectFit: "cover" }}
          />
        </div>
      );
    }
    return null;
  };

  return (
    <div
      className="project-detail-container"
      style={{
        width: "100%",
        height: "100%",
        padding: "20px",
        boxSizing: "border-box",
        overflow: "auto",
        ["--project-detail-scrollbar-thumb" as string]: themes[theme]["--button-bg"],
      }}
    >
      <div
        style={{
          maxWidth: "800px",
          margin: "0 auto",
          color: themes[theme]["--color-text"],
          fontFamily: "monospace",
        }}
      >
        {/* Back Button */}
        <button
          onClick={() => navigate("/portfolio")}
          style={{
            padding: "8px 16px",
            backgroundColor: themes[theme]["--button-bg-light"],
            color: themes[theme]["--color-text"],
            border: "none",
            borderRadius: "20px",
            fontFamily: "monospace",
            cursor: "pointer",
            marginBottom: "20px",
            display: "flex",
            alignItems: "center",
            width: "fit-content",
          }}
        >
          ← Back
        </button>

        {/* Title */}
        <h1
          style={{
            color: themes[theme]["--color-accent-primary"],
            marginBottom: "20px",
          }}
        >
          {project.name}
        </h1>

        {/* Tech Chips */}
        <LanguageBadges />

        {/* Hero Thumbnail */}
        {project.image && (
          <div
            style={{
              width: "100%",
              aspectRatio: "16 / 9",
              borderRadius: "12px",
              overflow: "hidden",
              marginBottom: "30px",
            }}
          >
            <img
              src={project.image}
              alt={`${project.name} hero`}
              loading="lazy"
              decoding="async"
              style={{ width: "100%", height: "100%", objectFit: "cover" }}
            />
          </div>
        )}

        {/* Overview (short description) */}
        <div
          style={{
            backgroundColor:
              theme === "bunny"
                ? "rgba(121, 85, 189, 0.1)"
                : "rgba(8, 34, 163, 0.1)",
            padding: "20px",
            borderRadius: "12px",
            marginBottom: "30px",
          }}
        >
          <h3
            style={{
              color: themes[theme]["--color-accent-primary"],
              marginTop: 0,
            }}
          >
            Overview
          </h3>
          <p style={{ lineHeight: 1.6 }}>{project.description}</p>
        </div>

        {/* Rich Sections */}
        {project.sections.map(renderSectionContent)}

        {/* CTA */}
        <div
          style={{
            borderTop: `1px solid ${themes[theme]["--border-color"]}`,
            paddingTop: "20px",
            marginTop: "30px",
            display: "flex",
            justifyContent: "center",
          }}
        >
          <button
            onClick={() => navigate("/portfolio")}
            style={{
              padding: "10px 20px",
              backgroundColor: themes[theme]["--button-bg"],
              color: themes[theme]["--button-text"],
              border: "none",
              borderRadius: "20px",
              fontFamily: "monospace",
              cursor: "pointer",
            }}
          >
            View More Projects
          </button>
        </div>
      </div>

    </div>
  );
};

export default ProjectDetail;
